import { useRouter } from "next/router";
import { securePage } from "../../../components/common/secure_page";
import { Account } from "../../../hooks/use_account";
import { useCssStateManager } from "../../../hooks/use_css_state_manager";
import { SmartState } from "../../../hooks/use_smart_state";
import AccountFirstLoginAddressBody from "../../../components/account/first-login/address/body";
import Form from "../../../components/common/form/form";
import { formRequestAccountYourAddressesDelete } from "../../../lib/shared/form-request-makeup/requests/account/your-addresses/delete";
import { formParentYourAddresses } from "../your-addresses";
import Title from "../../../components/common/form/title";
import Box from "../../../components/common/form/box";
import P from "../../../components/common/form/p";
import AddressDisplay from "../../../components/account/your-addresses/address-block/address-display";
import { useStatusMessage } from "../../../hooks/use_status_message";
import { useCustomRouter } from "../../../hooks/use_custom_router";
import { useContext, useEffect } from "react";
import Spacer from "../../../components/common/spacer";
import SubmitButton from "../../../components/common/form/submit-button";
import { AlternateButton } from "../../../components/common/form/button";
import Input from "../../../components/common/form/input";
import { LayoutStateContext } from "../../../contexts/layout_state";
import { misc } from "../../../lib/misc";

export type AccountYourAddressesDeleteProps = {
    result: {};
    account: SmartState<Account | null>;
};
  
const AccountYourAddressesDelete = securePage(function({result,account}: AccountYourAddressesDeleteProps) {
    const queryParams = misc.getQueryParams();
    const id = queryParams["id"];
    
    const statusMessage = useStatusMessage(true);
    const router = useCustomRouter();

    const { user } = account.value;
    
    const address = user.addresses.filter(({id: _id}) => id===_id)[0];

    const buttonWidth = 90;

    useEffect(() => {
        if (!address) {
            statusMessage.loadMessage({
                type: "error",
                message: "Address not found"
            });
            router.push("/account/your-addresses");
        }
    },[]);

    const { routerIsLocked } = useContext(LayoutStateContext);

    if (!address) {
        return null;
    }

    return <Form 
        requestMakeup={formRequestAccountYourAddressesDelete}
        parent={formParentYourAddresses}
        onSuccess={() => {
            return {
                type: "redirect",
                path: "/account/your-addresses",
                message: {
                    type: "info",
                    message: "Your address has been deleted successfully."
                }
            };
        }}
    >
        <Title>Confirm Address Deletion</Title>
        <Box border>
            <P>
                Are you sure you want to delete the following address?
            </P>
            <Spacer size={13} type="vertical" />
            <AddressDisplay address={address} fontSize={14}/>
            <Spacer size={25} type="vertical" />
            <P small pale>
                <strong>Note</strong>: deleting this address will not affect any pending orders being shipped to it.
            </P>
            <Input name="addressId" type="hidden" initialValue={id} />
            <SubmitButton caption="Delete" inline fixedWidth={buttonWidth} />
            <Spacer type="horizontal" size={10} />
            <AlternateButton caption="Cancel" redirectToPath="/account/your-addresses" disabled={routerIsLocked.value} />
        </Box>
    </Form>;
});

export default AccountYourAddressesDelete;